@-webkit-keyframes spinner {
  0% {
    fill: #ffffff;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    fill: rgba(255, 255, 255, 0.25);
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}
@keyframes spinner {
  0% {
    fill: #ffffff;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    fill: rgba(255, 255, 255, 0.25);
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}
.loading-wrapper {
  background: #242d40;
  position: relative;
  width: 225px;
  height: 225px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 4px;
}
.loading-wrapper svg {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 160px;
    height: 160px;
    padding: 20px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
.loading-wrapper svg circle {
    fill: rgba(0, 0, 0, 0.25);
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-animation: spinner 1s infinite;
            animation: spinner 1s infinite;
    transform-box: fill-box;
  }
.loading-wrapper circle:nth-child(1) {
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
  }
.loading-wrapper circle:nth-child(2) {
    -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
  }
.loading-wrapper circle:nth-child(3) {
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
  }
.loading-wrapper circle:nth-child(4) {
    -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
  }
.loading-wrapper circle:nth-child(5) {
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
  }
.loading-wrapper circle:nth-child(6) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
  }
.loading-wrapper circle:nth-child(7) {
    -webkit-animation-delay: -0.3999999999999999s;
            animation-delay: -0.3999999999999999s;
  }
.loading-wrapper circle:nth-child(8) {
    -webkit-animation-delay: -0.29999999999999993s;
            animation-delay: -0.29999999999999993s;
  }
.loading-wrapper circle:nth-child(9) {
    -webkit-animation-delay: -0.19999999999999996s;
            animation-delay: -0.19999999999999996s;
  }
.loading-wrapper circle:nth-child(10) {
    -webkit-animation-delay: -0.09999999999999998s;
            animation-delay: -0.09999999999999998s;
  }
